  <router-view :users="users" :addToCart="addToCart" :mycart="cart" :subtotal="getsubtotal"
    :getprice="getprice" :getdesc="getdesc" :grandtotal="getgrandtotal" :getimage="getimage"/>
  
  
</template>

<script> 
import axios from 'axios'

export default {
   data () {
    return {
     // normally we stored all the products in this place dynamically.  
      showsidebar: true,
      cart: {},
      users: []
    }},
     mounted() {
    axios
      .get(`http://localhost/Phplessons`)
      .then((response) => {
      // JSON responses are automatically parsed.
        this.users = response.data
        console.log(response.data)
   })
      .catch((e) => {
        this.errors.push(e)
   })
},
    methods: {
       getimage(img){
      const product = this.users.find((p) => {
        return p.Name === img
        // what this means is that we have selected the name in the array and we arr storing it into something called name .
      })
      return product.images
    },
      getprice (name) {
      const product = this.users.find((p) => {
        return p.Name === name
        // what this means is that we have selected the name in the array and we arr storing it into something called name .
      })
      return product.Price
    },
     getdesc (desc) {
      const productdesc = this.users.find((p) => {
       return p.Name === desc
      })
      return productdesc.Description
    },
     getsubtotal () {
      const subtotal = Object.entries(this.cart).reduce((acc, curr) => {
          return acc + curr[1] * this.getprice(curr[0])
        },
        0
      )
      return subtotal.toFixed(2)
    },

     getgrandtotal () {
     const subtotal = Object.entries(this.cart).reduce((acc, curr) => {
          return acc + curr[1] * this.getprice(curr[0])
        },
        1000
      )
      return subtotal.toFixed(2)
    },
     addToCart(ourname, ourquantity){
     this.cart[ourname] = 0
     this.cart[ourname] += ourquantity
     console.log(this.cart)
    },
    Delete(onecartt){
      this.cart.splice(this.cart.indexOf(onecartt), 1)
    } 
  }
  }

</script> 

